/**
 * @ngdoc directive
 * @name troutDashApp.directive:StreamListDirective
 * @description
 * # StreamListDirective
 */
angular.module('troutSpotr')
  .directive('regionHeader', ['StreamApiService',
    function(StreamApiService) {
      return {
        //app/ui/main/main.html
        templateUrl: 'app/ui/streamList/region/RegionHeaderTemplate.html',
        restrict: 'A',
        link: function postLink(scope, element, attrs) {
          scope.displayedCounties = [];

          scope.init = function() {
            scope.clearCounties();  
          };

          scope.clearCounties = function() {
            scope.displayedCounties = [];
          };

          scope.loadMoreCounties = function(numberOfCountiesToAdd) {
            var numberOfDisplayedCounties = scope.displayedCounties.length;
            var numberOfChildCounties = scope.region.children.length;

            numberOfCountiesToAdd = numberOfCountiesToAdd == null ? 1 : numberOfCountiesToAdd;
            var numberOfCountiesRemaining = numberOfChildCounties - numberOfDisplayedCounties;
            numberOfCountiesToAdd = Math.min(numberOfCountiesRemaining, numberOfCountiesToAdd);
            var finalIndex = numberOfDisplayedCounties + numberOfCountiesToAdd;
            for (var i = numberOfDisplayedCounties; i < finalIndex; i++) {
              var countyToInject = scope.region.children[i];
              scope.displayedCounties.push(countyToInject);
            }
          };

          scope.getScrollContainer = function() {
            return '#js-list-container';
          };

          scope.getRegionScrollBodyId = function(region) {
            var result = '#' + scope.getRegionId(region);
            return result;
          };

          scope.getRegionId = function(region) {
            var result = 'hdr-region_' + region.id;
            return result;
          };

          scope.init();
        }
      };
    }
  ]);
'use strict';
/**
 * @ngdoc directive
 * @name troutDashApp.directive:StreamListDirective
 * @description
 * # StreamListDirective
 */
angular.module('troutSpotr')
  .directive('countyHeader', ['StreamApiService',
    function(StreamApiService) {
      return {
        //app/ui/main/main.html
        templateUrl: 'app/ui/streamList/county/CountyHeaderTemplate.html',
        restrict: 'A',
        link: function postLink(scope, element, attrs) {
          scope.displayedStreams = [];

          scope.init = function() {
            scope.clearStreams();  
          };

          scope.clearStreams = function() {
            scope.displayedStreams = [];
          };

          scope.loadMoreStreams = function(numberOfStreamsToAdd) {
            var numberOfDisplayedStreams = scope.displayedStreams.length;
            var numberOfChildStreams = scope.county.children.length;

            numberOfStreamsToAdd = numberOfStreamsToAdd == null ? 30 : numberOfStreamsToAdd;
            var numberOfStreamsRemaining = numberOfChildStreams - numberOfDisplayedStreams;
            numberOfStreamsToAdd = Math.min(numberOfStreamsRemaining, numberOfStreamsToAdd);
            var finalIndex = numberOfDisplayedStreams + numberOfStreamsToAdd;
            for (var i = numberOfDisplayedStreams; i < finalIndex; i++) {
              var streamToInject = scope.county.children[i];
              scope.displayedStreams.push(streamToInject);
            }
          };

          scope.getCountyScrollBodyId = function(county) {
            var result = '#' + scope.getCountyId(county);
            return result;
          };

          scope.getScrollContainer = function() {
            return '#js-list-container';
          };

          scope.getCountyId = function(county) {
            return 'hdr-county_' + county.id;
          };

          scope.init();
        }
      };
    }
  ]);
'use strict';

/**
 * @ngdoc directive
 * @name troutDashApp.directive:StreamListItemDirective
 * @description
 * # StreamListItemDirective
 */
angular.module('troutSpotr')
  .directive('streamListItemDirective', function() {
    return {
      templateUrl: 'app/ui/streamList/streamListItem/streamlistitemtemplate.html',
      restrict: 'A',
      link: function postLink(scope, element, attrs) {
        scope.isSmall = true;
        scope.isAlertSymbolDisplayed = function() {
          if (scope.isSmall === false) {
            return false;
          }

          var isRestrictions = scope.stream.Restrictions.length > 0;
          var isMessage = scope.stream.AlertMessage != null && scope.stream.AlertMessage.length > 0;

          if (isRestrictions && isMessage) {
            return true;
          }
        };

        scope.getAlertMessage = function() {
          return scope.stream.AlertMessage;
        };

        scope.expand = function() {
          scope.isSmall = !scope.isSmall;
          console.log(scope.stream);
        };

        scope.getCounties = function() {
          if (!scope.stream.Counties) {
            return null;
          }

          return scope.stream.Counties.map(function(c) {
            return c.Name;
          }).join(',');
        };

        scope.getAlternativeNames = function() {
          if (!scope.stream.LocalNames) {
            return null;
          }

          if (scope.stream.LocalNames.length === 0) {
            return '';
          }

          return 'aka ' + scope.stream.LocalNames.join(',');
        };
      }
    };
  });
